*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

.container {
  width: 144rem;
  margin: 0 auto;
}

body {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  color: #121212;
  font-weight: 300;
  line-height: 150%;
}

.section-hero__title {
  font-size: 4.2rem;
  line-height: 5.04rem;
  margin-bottom: 2.4rem;
}

.section-hero__description {
  line-height: 2.4rem;
  font-size: 1.6rem;
  margin-bottom: 3.2rem;
}

.btn {
  display: block;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #e4c1ae;
  border-radius: 100px;
  max-width: 12.8rem;
  height: 4.8rem;
}
.btn__link {
  text-decoration: none;
  color: #fff;
  font-weight: 700;
  font-size: 1.6rem;
}

.section-hero {
  margin: 9.6rem 23rem 0 23rem;
}
.section-hero-box {
  display: flex;
  gap: 9.5rem;
}
.section-hero-text-box {
  display: flex;
  flex-direction: column;
  width: 38rem;
  justify-content: center;
}
.section-hero__img {
  width: 35%;
  height: 35%;
}